form {
    display: block;
    margin-top: 0em;
}
label {
    color: white;
}
input[type="date"], input[type="time"] {
    height: 38px;
    padding: 6px 10px;
    background-color: #fff;
    border: 1px solid #D1D1D1;
    border-radius: 4px;
    box-shadow: none;
    box-sizing: border-box;
}